<template>
      <div>
    
        <div class="row row-sm">
            <div class="col-md-12 p-0">
                <div class="card">
                    <div class="card-header bg-transparent">
                        <div class="d-flex">
                            <div class="mr-auto">
                                 <div class="btn-group-xs ">
                                     
                                        <button class="btn btn-secondary"><i class="icon ion-android-list"></i> </button>
                                        <button class="btn btn-secondary" ><i class="icon ion-star"></i> </button>
                                        <button class="btn btn-secondary" ><i class="icon ion-refresh"></i> </button>
                                        <router-link :to="{name:'BoardWrite',query:{prj_id:$parent.selected.projectId,bbs_id:$route.query.bbs_id,pageNo:pager.pageNo}}"  class="btn btn-primary text-white"  ><i class="icon ion-edit"></i> </router-link>
                                 </div>
                                

                            </div>
                            <div class="d-flex">
                                <div class="mr-auto">
                                    <input class="form-control  form-control-sm form-input" type="text" name="firstname" value="John Paul" placeholder="Enter firstname">
                                </div>
                                <div class="btn-group-xs ">
                                 &nbsp;<button class="btn btn-primary"></button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="card-body p-0">

                        <div class="">
                              <table class="table default table-sm mg-b-0 bd-0">
                                  <colgroup>
                                    <col width="150px"/>
                                    <!-- <col width="150px"/> -->
                                    <col width="*"/>
                                    <col width="250px"/>
                                    <col width="250px"/>
                                  </colgroup>
                                  <thead>
                                      <tr>
                                          <th class="text-center"></th>
                                          <!-- <th class="text-center"></th> -->
                                          <th></th>
                                          <th></th>
                                          <th></th>
                                      </tr>            
                                  </thead>
                                  <tbody>
                                      <tr v-for="item in listBoardContent"  v-bind:key="item.seq">
                                            <td class="text-center"><i class="icon ion-ios-star-outline"></i></td>
                                            <!-- <td>{{item.category_title}}</td> -->
                                            <td>{{item.title}}</td>
                                            <td>{{item.create_user_name}}</td>
                                            <td>{{new Date(item.create_date).formatDateTime()}}</td>
                                        </tr>
                                    
                                  </tbody>
                              </table>
                        </div>
                    
                    </div>
                </div>
                
            </div>
        </div>
         <div class="text-center pt-3 pb-3">
               <span> 1 / 30</span>
                            <a href="" class="btn btn-secondary  btn-sm"></a>
                            <a href="" class="btn btn-primary  btn-sm">1</a>
                            <a href="" class="btn btn-primary  btn-sm">2</a>
                            <a href="" class="btn btn-primary btn-sm">3</a>
                            <a href="" class="btn btn-primary btn-sm">4</a>
                            <a href="" class="btn btn-primary btn-sm">5</a>
                            <a href="" class="btn btn-primary btn-sm">6</a>
                            <a href="" class="btn btn-primary btn-sm">7</a>
                            <a href="" class="btn btn-primary btn-sm">8</a>
                            <a href="" class="btn btn-primary btn-sm">9</a>
                            <a href="" class="btn btn-primary btn-sm">10</a>
                            <a href="" class="btn btn-secondary  btn-sm"></a>
                          
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
export default {
    data: function() {
        return {
            listBoardContent:[],
            pager: {
                model: {},
                pageNo: 1,
                totalSize: 0,
                rowSize: 10,
                blockSize: 10,
                list: [1, 2],
                callBack: function(n) {
                    //this.model.fnGetList(null);
                }
            }
        }
    },
    created: function() {

        
    },
    components: {

    },
    mounted: function() {
        this.list();
    },
    methods: {
        init:function(){
            console.log("init list");
            this.list();
        },
        list:function(){
            
            let _this=this;
            let url=`${domain}/board/content/list/${this.$parent.selected.projectId}/${this.$route.query.bbs_id}`;
            Axios.get(url)
            .then(function(response){
                let rs=response.data;
                _this.listBoardContent=rs.listBoardContent;
            })
            .catch(function(ex){
                console.log(ex);
            });
        }
    }
}
</script>



