<template>
  <div class="ht-40 d-flex align-items-center justify-content-center mg-b-20">
    <ul class="pagination pagination-basic pagination-rounded mg-b-0">
      <li class="page-item hidden-xs-down" v-if="pass.startBlock>1" v-on:click="onClickPagerNo(1)">
        <a class="page-link" href="#" aria-label="First"><i class="fa fa-angle-double-left"></i></a>
      </li>
      <li class="page-item" v-if="pass.startBlock>1" v-on:click="onClickPagerNo(pass.startBlock-1)">
        <a class="page-link" href="#" aria-label="Previous"><i class="fa fa-angle-left"></i></a>
      </li>
      <li class="page-item" v-on:click="onClickPagerNo(item)" v-bind:class="{'active':item==pass.pageNo}" v-for="item in pass.list" v-bind:key="item">
        <a class="page-link" href="#" >{{item}}</a>
      </li>
      <li class="page-item" v-on:click="onClickPagerNo(pass.nextBlock)">
        <a class="page-link" href="#" aria-label="Next"><i class="fa fa-angle-right"></i></a>
      </li>
      <li class="page-item hidden-xs-down" v-on:click="onClickPagerNo(pass.totalSize)">
        <a class="page-link" href="#" aria-label="Last"><i class="fa fa-angle-double-right"></i></a>
      </li>
    </ul>
  </div>



<!-- 
  <div class="dataTables_paginate paging_simple_numbers float-right" id="xp-default-datatable_paginate">
    <ul class="pagination">
      <li class="paginate_button page-item previous disabled" id="xp-default-datatable_previous" v-if="pass.startBlock>1" v-on:click="onClickPagerNo(pass.startBlock-1)">
        <a href="#" aria-controls="xp-default-datatable" data-dt-idx="0" tabindex="0" class="page-link">Previous</a>
      </li>
      <li class="paginate_button page-item" v-on:click="onClickPagerNo(item)" v-bind:class="{'active':item==pass.pageNo}" v-for="item in pass.list" v-bind:key="item">
        <a href="#" aria-controls="xp-default-datatable" data-dt-idx="1" tabindex="0" class="page-link">{{item}}</a>
      </li>
      <li class="paginate_button page-item next" id="xp-default-datatable_next" v-on:click="onClickPagerNo(pass.nextBlock)">
        <a href="#" aria-controls="xp-default-datatable" data-dt-idx="7" tabindex="0" class="page-link">Next</a>
      </li>
    </ul>
  </div> -->
</template>
<script>
  export default{
    props:['pass']
    ,methods:{
      onClickPagerNo:function(n){
        this.pass.pageNo=n==0?1:n;
        this.pass.callBack(n);
      }
      ,createPaging:function(pager,vm,cbType){
        pager.callBackType=cbType;
        let _list=[];
        let _endPagerNo=parseInt((pager.totalSize-1)/pager.rowSize)+1;

        let _startBlock=parseInt((pager.pageNo-1)/pager.blockSize)*pager.blockSize+1;
        let index=0;
        let _endBlock=0;
        let _nextBlock=0;

        for(let i=_startBlock;i<=_endPagerNo;i++){
          if(index<pager.blockSize){
            _list.push(i);
            _endBlock=i;
            if(i<_endPagerNo){
              _nextBlock=i+1;
            }else{
              _nextBlock=0;
            }
            index++;
          }else{
            break; 
          }
        }//end for
        pager.list=_list;
        pager.model=vm;
        pager.startBlock=_startBlock;
        pager.nextBlock=_nextBlock;
        return pager;
      }
    }
  }
</script>



