<template>
  <div>
  <div class="row row-sm">
            <div class="col-sm-12 col-lg-12 p-0">
              <div class="card shadow-base">
                    <div class="card-header bg-transparent">
                          <h5 class="tx-center tx-uppercase tx-inverse tx-semibold tx-spacing-1">
                           <a href="#" v-on:click="onClickPrev(currentMonth)"> <i class="fa fa-angle-left"></i></a> {{currentYear}} {{currentMonth}}
                           <a href="#" v-on:click="onClickNext(currentMonth)"> <i class="fa fa-angle-right"></i> </a>
                           </h5>
                    </div>
                    <div class="card-body">
                        <div class="row">
                            <div class="col-md-4">
                                <h1 class="tx-100 tx-center">10.21</h1>
                                <div class="form-group">
                                    <label class="form-control-label">: <span class="tx-danger">*</span></label>
                                    <select class="form-control form-control-sm form-input"  name="firstname" value="John Paul">
                                        <option value="Firefox">Firefox</option>
                                        <option value="Chrome">Chrome</option>
                                        <option value="Safari">Safari</option>
                                        <option value="Opera">Opera</option>
                                        <option value="Internet Explorer">Internet Explorer</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <label class="form-control-label">: <span class="tx-danger">*</span></label>
                                    <input class="form-control form-control-sm form-input" type="text" name="firstname" value="John Paul" placeholder="Enter firstname">
                                </div>
                                <div class="form-group">
                                    <label class="form-control-label">: <span class="tx-danger">*</span></label>
                                    <textarea class="form-control form-control-sm form-input"  rows="5"></textarea>

                                </div>
                                <div class="form-group">
                                    <label class="form-control-label">: <span class="tx-danger">*</span></label>
                                    <textarea class="form-control form-control-sm form-input"   rows="5"></textarea>

                                </div>
                                <div>
                                    <button class="btn btn-block btn-primary"></button>
                                </div>
                            </div>
                            <div class="col-md-8">
                                <div class="bd table-responsive calendar">
                                  <table class="table table-bordered mg-b-0 ">

                                    <colgroup>
                                      <col>
                                    </colgroup>
                                    <thead>
                                      <tr>
                                        <th v-for="(weekName, index) in weekNames" v-bind:key="index">
                                         <span  v-bind:class="{'tx-danger':weekName=='','tx-primary':weekName==''}">{{weekName}}</span>
                                          
                                        </th>
                                      </tr>
                                    </thead>
                                    <tbody>
                                      <tr v-for="(row, index) in currentCalendarMatrix" :key="index">
                                        <td v-for="(day, index2) in row" :key="index2">

                                           <div class="d-flex"><div class="mr-auto">
                                          <h6 v-if="isToday(currentYear, currentMonth, day)" class="active selected" v-on:click="onClickDay(currentYear, currentMonth, day)">
                                            {{day}}
                                          </h6>
                                          <h6 v-else  v-bind:class="{'tx-danger':index2==6,'tx-primary':index2==5}" v-on:click="onClickDay(currentYear, currentMonth, day)" >
                                            {{day}}
                                          </h6>
                                          </div>
                                          <div>(8)</div>
                                           </div>
                                        </td>
                                      </tr>
                                    </tbody>
                                </table>   
                                  </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      weekNames: ["", "", "","", "", "", ""],
      rootYear: 1904,
      rootDayOfWeekIndex: 4, // 2000 1 1 
      currentYear: new Date().getFullYear(),
      currentMonth: new Date().getMonth()+1,
      currentDay: new Date().getDate(),
      currentMonthStartWeekIndex: null,
      currentCalendarMatrix: [],
      endOfDay: null,
      memoDatas: [],
    }
  },
  mounted(){
      this.init();
  },
  methods: {
      init:function(){
        this.currentMonthStartWeekIndex = this.getStartWeek(this.currentYear, this.currentMonth);
        this.endOfDay = this.getEndOfDay(this.currentYear, this.currentMonth);
        this.initCalendar();
      },
      initCalendar:function(){
        this.currentCalendarMatrix = [];
        let day=1;
        for(let i=0; i<6; i++){
          let calendarRow = [];
          for(let j=0; j<7; j++){
            if(i==0 && j<this.currentMonthStartWeekIndex){
              calendarRow.push("");
            }
            else if(day<=this.endOfDay){
              
              calendarRow.push(day.format);
              day++;
            }
            else{
              calendarRow.push("");
            }
          }
          this.currentCalendarMatrix.push(calendarRow);
        }
      },
      onClickDay:function(currentYear, currentMonth, day){
        this.daddy.callBack(currentYear, currentMonth, day);
      },
      getEndOfDay: function(year, month){
          switch(month){
              case 1:
              case 3:
              case 5:
              case 7:
              case 8:
              case 10:
              case 12:
                return 31;
                break;
              case 4:
              case 6:
              case 9:
              case 11:
                return 30;
                break;
              case 2:
                if( (year%4 == 0) && (year%100 != 0) || (year%400 == 0) ){
                return 29;   
                }
                else{
                    return 28;
                }
                break;
              default:
                console.log("unknown month " + month);
                return 0;
                break;
          }
      },
      getStartWeek: function(targetYear, targetMonth){
        let year = this.rootYear;
        let month = 1;
        let sumOfDay = this.rootDayOfWeekIndex;
        while(true){
          if(targetYear > year){
            for(let i=0; i<12; i++){
              sumOfDay += this.getEndOfDay(year, month+i);
            }
            year++;
          }
          else if(targetYear == year){
            if(targetMonth > month){
              sumOfDay += this.getEndOfDay(year, month);
              month++;
            }
            else if(targetMonth == month){
              return (sumOfDay)%7;
            }
          }
        }
      },
      onClickPrev: function(month){
        month--;
        if(month<=0){
          this.currentMonth = 12;
          this.currentYear -= 1;
        }
        else{
          this.currentMonth -= 1;
        }
        this.init();
      },
      onClickNext: function(month){
        month++;
        if(month>12){
          this.currentMonth = 1;
          this.currentYear += 1;
        }
        else{
          this.currentMonth += 1;
        }
        this.init();
      },
      isToday: function(year, month, day){
        let date = new Date();
        return year == date.getFullYear() && month == date.getMonth()+1 && day == date.getDate(); 
      }
  }
}
</script>

<style type="text/css">
.rounded {
      -moz-border-radius:15px 15px 15px 15px; 
      border-radius:15px 15px 15px 15px;
      border:solid 1px #ffffff;
      background-color:#ffffff;
      padding:5px;
      color:#292929;
      cursor: pointer;
    }
    .rounded.today{
      color:#ffffff;
      background-color:#2b6bd1;
      
    }
</style>


