import Vue from 'vue';
import VueRouter from 'vue-router';
import Default from './components/default.vue'
import BoardDetail from './components/board/detail.vue'
import BoardWrite from './components/board/write.vue'
import BoardList from './components/board/list.vue'
import Calendar from './components/calendar.vue'
import ListUsers from './components/listUsers.vue'
import Blog from './components/blog.vue'
import Meeting from './components/meeting.vue'
import ProjectStatus from './components/project/kanban.vue'
import es6 from 'es6-promise/auto'
import Axios from 'axios';
import StringProtoTypes from '../uitls/string-prototypes.js'
Vue.use(VueRouter);

const routes = [
  { path: '/', component: Default, name: 'Default'}
  ,{ path: '/project/status', component: ProjectStatus, name: 'ProjectStatus'}
  ,{ path: '/board/detail', component: BoardDetail, name: 'BoardDetail'}
  ,{ path: '/board/write', component: BoardWrite, name: 'BoardWrite'}
  ,{ path: '/board/list', component: BoardList, name: 'BoardList'}
  ,{ path: '/calendar', component: Calendar, name: 'Calendar'}
  ,{ path: '/list/users', component: ListUsers, name: 'ListUsers'}
  ,{ path: '/blog', component: Blog, name: 'Blog'}
  ,{ path: '/meeting', component: Meeting, name: 'Meeting'}
];

const router = new VueRouter({
  routes // short for `routes: routes`
});

new Vue({
  router,
  created(){
    let obj=this;
    Axios.get(domain+"/api/project/list")
    .then(function(response){
      obj.listProject=response.data.listProject;
    })
    .catch(function(ex){
      console.log(ex);
    });
    
  },
  // watch:{
 
  //   $route(to, from){
    
  //     this.$children.forEach(element => {
  //       try{ 
  //         console.log("watch");
  //         console.log(element);
  //         element.init();
  //       }catch(ex){
  //         console.log(ex);
  //       }
      
  //     });
  //   }
  // } ,
  mounted() {
  //  console.log("root mounted");
  },
  data:function(){
    return {
      listProject:[]
      ,selected:{
        projectId:'prj-00'
      }
    }
  },
  methods:{
    fnOnClickGoProject:function(){
      window.location=`#/board/list?prj_id=${this.selected.projectId}&bbs_id=${router.currentRoute.query.bbs_id}`;
    }
  }
}).$mount('#app');



